/** utilidades**/
.no-margin {
  margin: 0; }

.no-pading {
  padding: 0; }

.centrar-texto {
  text-align: center; }

:root {
  --fuenteHeading: 'PT Sans', sans-serif;
  --fuenteParrafos: 'Open Sans', sans-serif;
  --primario:#AEBDAC;
  --segundario:#576855;
  --grisOscuro:#424242;
  --gris:#e1e1e1;
  --blanco: #ffffff;
  --negro:#000000;
  --rojo: #a80d0d; }

.dark-mode {
  background-color: #0e1616; }
  .dark-mode .sobre-nosotros p {
    color: #d1cfcf; }
  .dark-mode .blog-entrada-contenido p {
    color: #d1cfcf; }
  .dark-mode .header-contenido-logos img {
    filter: invert(100%); }
  .dark-mode .mobile-menu img {
    filter: invert(100%); }
  .dark-mode .footer-contenido-seccion-redes img {
    filter: invert(100%); }
  .dark-mode h1 {
    color: #AEBDAC; }
  .dark-mode h2 {
    color: #AEBDAC; }
  .dark-mode label {
    color: #AEBDAC; }

/** UI **/
.blog-titulo {
  color: var(--segundario);
  font-weight: 300; }

.blog-entrada {
  max-width: 90%;
  border-bottom: solid 0.2rem;
  border-color: var(--primario); }
  .blog-entrada h2 {
    font-size: 2rem;
