  .blog-entrada-contenido img {
    width: 50%; }

.footer-contenido {
  max-width: 120rem;
  width: 95%;
  border-top: 0.5rem solid var(--grisOscuro);
  margin: auto;
  display: flex;
  justify-content: space-evenly;
  margin-top: 5rem;
