.formulario legend {
  font-size: 2rem;
  color: var(--segundario); }

.formulario label {
  font-weight: 700;
  text-transform: uppercase; }

.formulario textarea {
  height: 20rem; }

.formulario h2 {
  text-align: center;
  font-weight: 300; }

@media (max-width: 768px) {
