    font-weight: 700;
    text-transform: uppercase;
    display: block; } }

@media (max-width: 768px) {
  .formulario input:not([type="submit"]),
  textarea,
  select {
    padding: 1rem;
    width: 100%;
    background-color: var(--primario);
