    display: block; } }

.formulario input:not([type="submit"]),
textarea,
select {
  padding: 1rem;
  width: 100%;
  background-color: var(--primario);
  border: 1px solid var(--gris);
  border-radius: 1rem;
  margin-bottom: 2rem; }

.forma-contacto {
  max-width: 50rem;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.formulario input [type="radio"] {
  margin: 0;
  width: auto; }

.header-barra {
  text-align: center;
  background-color: var(--primario);
  padding-top: .3rem;
  padding-bottom: .3rem; }
  .header-barra h1 {
    font-family: var(--fuenteHeadin);
    font-size: 1.5rem;
    font-weight: 400;
    color: var(--grisOscuro); }

@media (max-width: 768px) {
  .header-contenido {
    display: flex;
    flex-direction: column;
    align-items: center; } }

/** media query**/
.header-contenido {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 120rem;
  width: 75%;
  margin: 0 auto;
  padding-top: 5rem;
  padding-bottom: 5rem; }

.header-contenido-logocom img {
  height: 10rem;
  border: 0.5rem solid var(--primario);
  border-radius: 1rem; }

.header-contenido-logos {
  height: 10rem;
  width: 20rem;
  display: flex;
  justify-content: space-between;
  padding-left: 1rem;
  padding-right: 1rem;
  align-items: center; }
  .header-contenido-logos img {
    height: 3rem; }

@media (max-width: 768px) {
  .header-contenido-barra {
    display: flex;
    flex-direction: column;
    align-items: center;
    visibility: hidden;
    opacity: 0;
    transition: visibility .3s ease-in-out, opacity 0.5s linear; } }

@media (min-width: 768px) {
  .mobile-menu {
    display: none; } }

.mostrar {
  visibility: visible;
  opacity: 1; }

.header-contenido-barra {
  margin: auto;
  text-align: center; }
  .header-contenido-barra a {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    color: var(--segundario); }

.header-contenido-barra a:hover {
  color: var(--primario); }

.dark-mode-boton:hover {
  cursor: pointer;
  filter: invert(50%); }

/** main **/
main {
  display: flex;
  flex-direction: column;
  align-items: center; }

.main-contenido {
  display: flex;
  max-width: 120rem;
  width: 100%; }
  .main-contenido img {
    width: 100rem;
    height: 50rem;
    border-radius: 1rem; }

@media (max-width: 768px) {
  .main-contenido {
    display: flex;
    flex-direction: column; } }

@media (max-width: 768px) {
  .main-contenido h2 {
